@import "./colors";

.page-footer {
	background-color: $bg-footer;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.page-footer-wrap {
	display: flex;
	flex-flow: row wrap;
	justify-content: center;
}

.footer-content {
	padding: 50px 25px 0px 25px;
	max-width: 250px;
	// .footer-content__title

	&__title {
		font-size: 14px;
		line-height: 16px;
		color: #fff;
		text-transform: uppercase;
		font-weight: 600;
		margin-bottom: 20px;
	}
}

.col-m-1 {
	&__item {
		padding: 5px 0;
	}

	// .col-m-1__item--link

	&__item--link {
		font-size: 14px;
		color: $text;
		text-decoration: none;

		&:hover {
			color: #fff;
		}
	}
}

.footer-info-box {
	&__items--list {
		padding-bottom: 25px;
	}
}

.items-box {
	display: flex;
	align-items: center;
	padding-bottom: 8px;
	// .items-box__icon

	&__icon {
		font-size: 20px;
		color: #fff;
	}

	// .items-box__text
	&__text {
		color: #fff;
		padding-left: 10px;
		transition: .3s ease;
	}

	.hover:hover {
		color: red;
	}
}

.items-info {
	font-size: 14px;
	font-style: normal;
	font-weight: 400;
	line-height: 1.64285714;
}

.newsletter {
	// .newsletter__title
	&__title {
		font-size: 14px;
		line-height: 16px;
		color: #fff;
		text-transform: uppercase;
		font-weight: 600;
		margin-bottom: 20px;
	}

	// .newsletter__text

	&__text {
		font-size: 14px;
		font-style: normal;
		font-weight: 400;
		line-height: 1.64285714;
	}
}

.control {
	display: flex;
	margin-top: 10px;

	// .control__email
	&__email {
		width: 50%;
		position: relative;
	}

	// .control__email--input
	&__email--envelope {
		position: absolute;
		z-index: 1;
		top: 50%;
		left: 8px;
		transform: translateY(-50%);
	}

	&__email--input {
		padding: 0 15px 0 30px;
		box-sizing: border-box;
		margin-bottom: 0;
		font-size: 12px;
		transition: margin-bottom .3s ease;
		background: #fff;
		border: 1px solid #e1e1e1;
		border-radius: 0;
		font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
		height: 42px;
		line-height: 1.64285714;
		vertical-align: baseline;
		width: 100%;
		box-sizing: border-box;
	}
}

.actions {
	width: 50%;
}

.primary {
	background-image: none;
	background: #ee3427;
	border: 2px solid #ee3427;
	color: #fff;
	cursor: pointer;
	display: inline-block;
	font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
	font-weight: 700;
	height: 100%;
	font-size: 14px;
	line-height: 1.6rem;
	box-sizing: border-box;
	vertical-align: middle;
	text-transform: uppercase;
	width: 100%;
	transition: .3s all ease;

	&:hover {
		background-color: #222;
		border: 2px solid #222;
	}
}

.social-links {
	display: flex;
	justify-content: space-between;
	margin-top: 50px;
}

.position-footer {
	li {
		width: 40px;
		height: 40px;
		background: #3a3939;
		border-radius: 50%;
		text-align: center;
		transition: .3s all ease;
		display: flex;
		justify-content: center;
		align-items: center;

		a {
			color: #fff;
		}
	}

	li:hover {
		background-color: #fff;

		a {
			color: #222;
		}
	}
}

.copyright {
	margin: 20px 0;
	font-size: 12px;
}

// @media (min-width: 991px) and (max-width: 1199px) {
// }

// @media (min-width: 768px) and (max-width: 990px) {
// }

@media only screen and (min-width: 320px) and (max-width: 767px) {
	.page-footer-wrap {
		flex-direction: column;
		padding: 0 10px;
	}

	.col-m-1 {
		display: none;
	}

	.sub-menu__list.open {
		display: block;
	}

	.footer-content {
		padding: 40px 0 0 0;
		position: relative;
	}

	.footer-content__title {
		margin-bottom: 0;
		position: relative;
	}

	.footer-content {
		max-width: 400px;
	}

	.control {
		flex-direction: column;
	}

	
	.control__email {
		width: 100%;
	}

	.actions {
		width: 100%;
		height: 42px;
		margin-top: 10px;
	}

	.footer-content__title::before {
		position: absolute;
		top: 60%;
		right: 0;
		width: 23px;
		height: 23px;
		margin-top: -13px;
		border-radius: 2px;
		box-sizing: border-box;
		text-align: center;
		font-size: 14px;
		line-height: 1;
		padding: 6px 0;
		border: 1px solid #fff;
	}

	.footer-content__title::before {
		content: "â–¼";
		display: inline-block;
		font-family: 'theme-icons';
		font-weight: 400;
		text-rendering: auto;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		transform: translate(0, 0);
	}
}
